<template>
	<Tabs value="system" class="tab">
		<Tab-pane label="新建账号" name="system" class="tab-item boxPanle">
			<Form ref="form" :model="formItem" :label-width="80" :rules="ruleValidate" class="from-self">
				<Form-item label="账号" prop="accountName">
					<Input v-model="formItem.accountName" placeholder="字母或字母和数字的组合" style="width: 400px"></Input>
				</Form-item>
				<Form-item label="密码" prop="password">
					<Input type="password" :maxlength="20" v-model="formItem.password" placeholder="请输入字母和数字密码（不少于6位）"
						   style="width: 400px"></Input>
				</Form-item>
				<Form-item label="使用人" prop="userName">
					<Input v-model="formItem.userName" placeholder="填写使用人名字" style="width: 400px"></Input>
				</Form-item>
				<Form-item label="角色" prop="roleName">
					<Select v-model="formItem.roleId" @on-change="selectChange" placeholder="请选择角色"
							style="width: 400px">
						<Option v-for="item in roleList" :disabled="item.roleId==1000"  :value="item.roleId" :key="item">{{item.roleName}}</Option>
					</Select>
				</Form-item>
				<Form-item label="描述">
					<Input v-model="formItem.roleComment" disabled placeholder="角色职责描述" style="width: 400px"></Input>
				</Form-item>
				<Form-item>
					<i-button type="primary" size="large" @click="submit('form')">确定</i-button>
					<i-button type="ghost" size="large" style="margin-left: 8px" @click="$router.go(-1)">取消</i-button>
				</Form-item>
			</Form>
		</Tab-pane>
		<i-button type="text" slot="extra" class="extra-btn" icon="ios-undo-outline" @click="$router.go(-1)">返回</i-button>
	</Tabs>
</template>
<script>
	let validateAccountName = (rule, value, callback) => {
		let regex = /^[a-z0-9_-]{3,16}$/;
		if (!regex.exec(value)) {
			callback(new Error('用户名应由3至16位字母或数字组成'));
		} else {
			callback();
		}
	};
	let validatePassword = (rule, value, callback) => {
		let regex = /^[a-z0-9_-]{6,20}$/;
		if (!regex.exec(value)) {
			callback(new Error('密码应由6至20位字母或数字组成'));
		} else {
			callback();
		}
	};
	export default {
		data () {
			return {
				formItem: {
					accountName: '',
					password: '',
					userName: '',
					roleName: '',
					roleId: '',
					roleComment: ''
				},
				ruleValidate: {
					accountName: [
						{required: true, message: '用户名不能为空', trigger: 'blur'},
						{required: true, trigger: 'blur', validator: validateAccountName}
					],
					password: [
						{required: true, message: '密码不能为空', trigger: 'blur'},
						{required: true, trigger: 'blur', validator: validatePassword}
					],
					userName: [
						{required: true, message: '使用人不能为空', trigger: 'blur'}
					],
					roleId: [
						{type: 'number', required: true, message: '请选择角色', trigger: 'change'}
					]
				},
				roleList: [],
			}
		},
		created: function () {
			this.loadRoleName();
		},
		methods: {
			submit: function (name) {
				let _this = this;
				this.$refs[name].validate((valid) => {
					if (valid) {
						_this.$Modal.confirm({
							title: '确认新建账号',
							content: '<p>确认新建账号?</p>',
							loading: true,
							onOk: () => {
								_this.$http.post('/user/creatUser', {
									accountName: _this.formItem.accountName,
									password: _this.formItem.password,
									userName: _this.formItem.userName,
									roleId: _this.formItem.roleId
								})
									.then(function (response) {
										if (response.data.code == 1) {
											_this.$Modal.remove();
											_this.$Message.success({
												content: response.data.msg
											});
											_this.$router.push('/system');
										} else {
											_this.$Message.error({
												content: response.data.msg,
												duration: 3,
												onClose: function () {
													_this.$Modal.remove();
												}
											});
										}
									})
									.catch(function (error) {
										_this.$Message.error({
											content: error,
											duration: 3,
											onClose: function () {
												_this.$Modal.remove();
											}
										});
									});
							}
						});
					} else {
						this.$Message.error('表单验证失败!');
					}
				})

			},
			selectChange: function (val) {
				let _this = this;
				_this.formItem.roleComment = _.result(_.find(_this.roleList, {'roleId': val}), 'roleComment');
			},
			loadRoleName: function () {
				let _this = this;
				this.$http.post('/auth/allRoleName', {})
					.then(function (response) {
						if (response.data.code == 1) {
							_this.roleList = response.data.data;
						} else {
							_this.$Message.error({
								content: response.data.msg,
								duration: 3
							});
						}

					})
					.catch(function (error) {
						_this.$Message.error({
							content: error,
							duration: 3
						});
					});
			}
		}
	}
</script>
<style>
	.tab {
		padding-top: 15px;
	}
	.boxPanle{
		min-height: 600px;
	}
	.ivu-tabs-tab {
		font-size: 16px;
	}

	.layout-content .extra-btn {
		font-size: 14px;
		color: #20abff;
	}

	.from-self {
		padding-top: 50px;
	}

	.block-item {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
	}
</style>



